.loading-bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #fff;
  z-index: 1000;
  animation: loadingAnimation 2s infinite;
}

.zoom-warning {
  position: absolute;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  padding: 10px;
  background-color: #ffdddd;
  color: #b30000;
  border: 1px solid #b30000;
  border-radius: 5px;
  z-index: 1000;
  font-weight: bold;
}

@keyframes loadingAnimation {
  0% { width: 0; }
  50% { width: 100%; }
  100% { width: 0; }
}

.errorbox {
  position: absolute;
  top: 50px;
  left: 10px;
  background-color: red;
  color: white;
  padding: 10px;
  border-radius: 5px;
  z-index: 1000;
}


.centered {
  position: fixed;
  top: calc(var(--vh, 1vh) * 50) !important;
  left: calc(var(--vw, 1vw) * 50) !important;
  /* z-index: 1000; */
  transform: translate(-50%, -50%) !important;  
}

.errorbox {
  color: 'red';
  position: 'absolute';
  top: 10;
  left: 10;
  z-index: 1000;
}
