.ant-menu-sub {
  background-color: rgba(0, 0, 0, 0.6) !important;
  /* font-family: 'UniNeueHeavyItalicr', cursive; */
  /* font-family: 'Alfa Slab One', display; */
  /* font-family: 'Bangers', display; */
}

/* submenu items */
.ant-menu-sub .ant-menu-title-content {
  color: #c9c5c2 !important;
  /* font-family: 'UniNeueHeavyItalic', cursive; */
  /* font-family: 'Alfa Slab One', display; */
  /* font-family: 'Bangers', display; */
  font-family: 'Telma-Variable', display;
  font-variation-settings: 'wght' 690;
  font-size: 20px;
}
.ant-menu-sub .ant-menu-title-content:hover {
  color: #fff !important;
}

.ant-menu-submenu-title {
  display: flex !important;
  padding: 0 2vh;
  position: fixed !important;
  /* top: 0 !important; */
  /* right: 0 !important; */
}
.ant-menu-sub .ant-menu-submenu-title {
  padding: 0 2vh;
  position: sticky !important;
  /* background-color: blue !important; */
}

.ant-menu-submenu {
  background-color: transparent !important;
}

.ant-menu-item-group-title {
  color: #c9c5c2 !important;
}
.ant-menu-item-group-title:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.ant-layout-header
/* .ant-menu-horizontal, */
/* .custom-menu .ant-menu-item  */ {
  justify-content: center !important;
  height: 80px;
  line-height: 80px;
}

.layoutstyle {
  min-height: calc(var(--vh, 1vh) * 100) !important;
  min-width: calc(var(--vw, 1vw) * 100) !important;
  width: calc(var(--vw, 1vw) * 100) !important;
}

.headerstyle {
  min-width: calc(var(--vw, 1vw) * 100) !important;
  width: calc(var(--vw, 1vw) * 100) !important;
}

.logoheader {
  height: 40px;
  position: fixed;
  margin-right: 80%;
  right: calc(var(--vw,1vw) * -1);
  top: 20px !important;
}

.settingpicker::-webkit-scrollbar {
  display: none;
}

.settingpicker {
  /* right: auto !important; */
  bottom: 0 !important;
  left: 0 !important;
  position: sticky;
  width: 68px !important;
  height: calc(var(--vh, 1vh) * 100 - 80px) !important;
  height: calc(100vh - 80px);
  overflow: scroll !important;
  /* background-color: #020305; */
}

.colorpicker::-webkit-scrollbar {
  display: none;
}

.colorpicker {
  /* right: auto !important; */
  bottom: 0 !important;
  /* top: 64px !important; */
  right: 0 !important;
  position: sticky;
  width: 48px !important;
  height: calc(var(--vh, 1vh) * 100 - 64px) !important;
  height: calc(100vh - 64px);
  overflow: scroll !important;
  /* background-color: #020305; */
}

.colorpicker > div {
  padding-left: 10px;
}

