  margin: 80px 0 0 0 !important; 
} */
.loading-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
}

.spinner {
  border: 6px solid #f3f3f3;
  border-top: 6px solid #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Smaller screen menu height */
@media (max-width: 768px) {
  .ant-layout-header,
  .ant-menu-horizontal,
  .custom-menu .ant-menu-item,
  .ant-menu > .ant-menu-submenu > .ant-menu-submenu-title {
    height: 60px;
    line-height: 60px;
    font-size: 18px;
  }
  .colorpicker {
    height: calc(var(--vh, 1vh) * 100 - 60px) !important;
    height: calc(100vh - 60px);
  }
  .ant-menu-sub .ant-menu-title-content {
    font-size: 16px;
  }
  .custom-menu .ant-menu-item {
    min-width: 40px; /* Set a minimum width */
    max-width: 140px; /* Set a maximum width */
    margin: 0 1% 0 1%;
  }
  .ant-menu-submenu-title {
    /* top: 0 !important; */
    right: 0 !important;
  }
  /* .custom-menu .ant-menu-item.home {
    left: 0 !important;
    position: fixed !important;
  } */
  /* .mapdiv {
    margin: 60px 0 0 0 !important; 
  } */
  .logoheader {
    height: 30px;
    width: 40px;
    position: fixed;
    right: calc(var(--vw,1vw) * 7);
    top: 15px !important;
  }
  .ant-layout-header {
    /* margin-left: auto; */
    /* left: auto !important; */
    justify-content: end !important;
    /* background-color: #020305; */
  }
}

